<script>
import {
  mdiSquareEditOutline,
  mdiDotsVertical,
  mdiMagnify,
  mdiAbTesting,
  mdiAbacus,
  mdiAbjadArabic,
  mdiAbjadHebrew,
  mdiAbugidaDevanagari,
  mdiAbugidaThai,
  mdiAccessPoint,
  mdiAccessPointCheck,
  mdiAccessPointMinus,
  mdiAccessPointNetwork,
  mdiAccessPointNetworkOff,
  mdiAccessPointOff,
  mdiAccessPointPlus,
  mdiAccessPointRemove,
  mdiAccountAlertOutline,
  mdiAccountArrowLeftOutline,
  mdiAccountArrowRightOutline,
  mdiAccountBoxMultipleOutline,
  mdiAccountBoxOutline,
  mdiAccountCancelOutline,
  mdiAccountCashOutline,
  mdiAccountCheckOutline,
  mdiAccountChildOutline,
  mdiAccountCircleOutline,
  mdiAccountClockOutline,
  mdiAccountCogOutline,
  mdiAccountDetailsOutline,
  mdiAlarmLightOutline,
  mdiAlertBoxOutline,
  mdiAlertCircleCheckOutline,
  mdiAlertDecagramOutline,
  mdiAlertMinusOutline,
  mdiAlertOutline,
  mdiAlertPlusOutline,
  mdiCheckOutline,
  mdiClipboardOutline,
  mdiClipboardPlayOutline,
  mdiCloseOutline,
  mdiCloudCheckOutline,
  mdiCloudDownloadOutline,
  mdiCogOutline,
  mdiCompassOffOutline,
  mdiContactlessPaymentCircleOutline,
  mdiCrownOutline,
  mdiDeleteOutline,
  mdiDiamondOutline,
  mdiEmailOpenOutline,
  mdiEmoticonHappyOutline,
  mdiFileMultipleOutline,
  mdiFlaskEmptyOutline,
  mdiPencilOutline,
} from '@mdi/js'
import axios from 'axios'
import moment from 'moment'
import data from './datatable-data'

// const BaseUrl = 'https://apply.insure/'
const BaseUrl = 'http://18.232.91.105/'
// const BaseUrl = 'http://localhost:3000/'

export default {
  setup() {
    const statusColor = {
      /* eslint-disable key-spacing */
      Current: 'primary',
      Professional: 'success',
      Rejected: 'error',
      Resigned: 'warning',
      Applied: 'info',
      /* eslint-enable key-spacing */
    }

    return {
      delId: '',
      applink: '',
      applicationinfo: {
        name: '',
        address: '',
        email: '',
        phone: '',
      },
      baseurl: BaseUrl,
      componentkey: 0,
      search: '',
      isadmin: true,
      dialog: false,
      statusitem: 'Open',
      mdiDeleteOutline,
      mdiPencilOutline,
      switch1: true,
      sortKey: 'register_date',
      reverse: false,
      headers: [
        { text: 'NAME', value: 'name', class: 'my-header-style' },
        { text: 'ADDRESS', value: 'address', class: 'my-header-style' },
        { text: 'EMAIL', value: 'email', class: 'my-header-style' },
        { text: 'DATE APPLIED', value: 'register_date', class: 'my-header-style' },
        { text: 'TYPE', value: 'insuranceType', class: 'my-header-style' },
        {
          text: 'STATUS',
          value: 'status',
          class: 'my-header-style',
          sortable: false,
          width: '200px',
        },
        { text: 'LINK', value: 'link', class: 'my-header-style' },

        // { text: 'ACTION', value: 'actions' },
      ],
      applicantLst: [],
      sortApplicantLst: [],
      select: { report: 'Open', src: 'Open' },
      status: [
        { report: 'Open', src: 'Open' },
        { report: 'Proposed', src: 'Proposed' },
        { report: 'Closed', src: 'Closed' },
        { report: 'Won', src: 'Won' },
        { report: 'Lost', src: 'Lost' },
      ],
      options: {
        page: 1,
        itemsPerPage: 10,
        mustSort: true,
      },
      statusColor,

      // icons
      icons: {
        mdiSquareEditOutline,
        mdiDotsVertical,
      },
      current_page_status: {
        itemsPerPage: 10,
        page: 1,
      },
    }
  },
  created() {
    this.getUsers()
    this.setInterval()
  },

  methods: {
    async orderChange(res) {
      if (!res) {
        const sortedDates = this.applicantLst.sort((a, b) => moment(a.register_date) - moment(b.register_date))
        this.applicantLst = [...sortedDates]
      } else {
        const sortedDates = this.applicantLst.sort((a, b) => moment(b.register_date) - moment(a.register_date))
        this.applicantLst = [...sortedDates]
      }
    },
    setInterval() {
      setInterval(() => {
        this.getUsers()

        // const current_itemperpage = {}
      }, 300000)
    },
    appInformation(val) {
      this.applink = val.link
      this.applicationinfo.name = val.name

      this.applicationinfo.householdmember = val.householdmember
      if (this.applicationinfo.householdmember.length > 0) {
        for (let m = 0; m < this.applicationinfo.householdmember.length; m++) {
          const date = new Date(this.applicationinfo.householdmember[m].birthday)
          const hour = date.getHours()
          const min = date.getMinutes()
          const sec = date.getSeconds()
          this.applicationinfo.householdmember[m].birthday = `${
            date.getMonth() + 1
          }-${date.getDate()}-${date.getFullYear()}`
        }
      }
      this.applicationinfo.agent = val.agent ? val.agent : [{ email: '', name: '', phone: '' }]
      this.applicationinfo.question5 = val.question5 ? val.question5 : ''
      this.applicationinfo.question6 = val.question6 ? val.question6 : ''
      this.applicationinfo.answer5 = val.answer5 ? val.answer5 : ''
      this.applicationinfo.answer6 = val.answer6 ? val.answer6 : ''
      this.applicationinfo.address = val.address
      this.applicationinfo.email = val.email
      this.applicationinfo.phone = val.phone
      this.applicationinfo.applicantIP = val.applicantIP
       this.applicationinfo.policyStartDate = moment(val.policyStartDate).format('MMMM Do YYYY') 
      this.applicationinfo.riskManagement = val.riskManagement
      this.applicationinfo.riskManagementQuestions = val.riskManagementQuestions
      this.applicationinfo.carrierType = val.carrierType
      this.applicationinfo.requestorcomments = val.requestorcomments
      this.applicationinfo.mailingadress = val.mailingadress
      this.applicationinfo.birthday = val.birthday
      this.applicationinfo.roof_shape = val.roof_shape
      this.applicationinfo.foundation = val.foundation
      this.applicationinfo.roof_type = val.roof_type
      this.applicationinfo.selectedUserType = val.selectedUserType
      this.applicationinfo.yearBuilt = val.yearBuilt
      this.applicationinfo.sqft = val.sqft
      this.applicationinfo.insuranceclaims = val.insuranceclaims
      this.applicationinfo.cardata = val.cardata
      if (this.applicationinfo.cardata.length > 0) {
        for (let i = 0; i < this.applicationinfo.cardata.length; i++) {
          this.applicationinfo.cardata[i].vininfo = ''
          if (val.vin[i]) {
            this.applicationinfo.cardata[i].vininfo = val.vin[i].vininfo
          }
        }
      }
      this.applicationinfo.security_system = val.security_system
      this.applicationinfo.currentadress = val.currentadress
      this.applicationinfo.current_auto_premiun = val.current_auto_premiun
      this.applicationinfo.dog = val.dog
      this.applicationinfo.shortrentals = val.shortrentals
      this.applicationinfo.propertyown = val.propertyown
      this.applicationinfo.multipleunit = val.multipleunit
      this.applicationinfo.policycancell = val.policycancell
      this.applicationinfo.swimmingpool = val.swimmingpool
      this.applicationinfo.register_date = val.register_date
      this.applicationinfo.quote_id = val.quote_id
      this.applicationinfo.license = val.license
      this.applicationinfo.agentemail = val.agentemail
      this.applicationinfo.insuranceType = val.insuranceType
      this.dialog = true
    },
    async Status(a, item) {
      item.status = a
      const data = { user: item }
      try {
        await axios.post(`${BaseUrl}status`, data).then(response => console.log(response))
        this.getUsers()
      } catch (err) {
        console.log(err)
      }
    },
    async getUsers() {
      const userdata = this.$cookies.get('user')
      console.log('again')
      const _that = this
      try {
        await axios.get(`${BaseUrl}allapplicant`).then(response => {
          this.applicantLst = response.data
          this.sortApplicantLst = response.data
        })
        if (this.applicantLst.length > 0) {
          for (let i = 0; i < this.applicantLst.length; i++) {
            if (this.applicantLst[i].address.includes('undefined')) {
              this.applicantLst[i].address = this.applicantLst[i].mailingadress
            }
            const date = new Date(this.applicantLst[i].register_date)
            this.applicantLst[i].timestap = date
            this.applicantLst[i].register_date = moment(this.applicantLst[i].register_date).format(
              'MM-DD-YYYY hh:mm:ss A',
            )
          }

          this.applicantLst.sort((x, y) => y.timestap - x.timestap)
        }
        if (userdata.role == 1) {
          this.headers = this.headers.filter(item => item.text !== 'TYPE' && item.text !== 'STATUS')
        } else {
          this.headers = this.headers.filter(item => item.text !== 'LINK')
          this.applicantLst = this.applicantLst.filter(
            item => item.agentemail == userdata.email && item.link == userdata.link,
          )
        }
        this.$cookies.set('applicantcounter', this.applicantLst.length)
        this.componentkey++
      } catch (err) {
        console.log(err)
      }
    },
  },
}
</script>
<style>
.v-sheet.v-card {
  border-radius: 40px;
  box-shadow: 0 0 0 0 !important;
}
.v-data-table th {
  font-weight: 600;
  background-color: #fff !important;
}
.text-start {
  color: #000 !important;
}
.theme--light.v-data-table td {
  color: #000 !important;
}
.v-text-field--outlined > .v-input__control > .v-input__slot {
  background: #fff !important;
}
.search-bar-responsive {
  position: fixed;
  top: 52px;
  left: 30%;
  width: 400px;
  z-index: 1;
}
.modalinfo {
  display: flex;
}
@media screen and (max-width: 1565px) {
  .search-bar-responsive {
    position: fixed;
    top: 52px;
    left: 30%;
  }
}
@media screen and (max-width: 1079px) {
  .search-bar-responsive {
    position: fixed;
    top: 142px;
    left: 30%;
  }
}
@media screen and (max-width: 996px) {
  .search-bar-responsive {
    position: fixed;
    top: 142px;
    left: 30%;
    width: 400px;
  }
}
@media screen and (max-width: 876px) {
  .search-bar-responsive {
    position: fixed;
    top: 142px;
    left: 30%;
    width: 320px;
  }
}
@media screen and (max-width: 715px) {
  .search-bar-responsive {
    position: fixed;
    top: 142px;
    left: 30%;
    width: 280px;
  }
}
@media screen and (max-width: 576px) {
  .search-bar-responsive {
    position: relative;
    top: 20px;
    left: 0%;
    width: 100%;
  }
  .modalinfo {
    display: block;
  }
}
.modalBg {
  background-color: #3a3af9 !important;
}
</style>
<template>
  <v-card>
    <template>
      <v-container class="d-flex">
        <v-switch
          v-model="switch1"
          class="ml-auto"
          :label="`Order By Desc: ${switch1.toString()}`"
          @change="orderChange(switch1)"
        ></v-switch>
      </v-container>
    </template>
    <v-data-table
      :key="componentkey"
      v-scroll="handleScroll"
      :headers="headers"
      :items="applicantLst"
      :multi-sort="false"
      :disable-sort="false"
      item-key="register_date"
      class="table-rounded"
      :search="search"
      :footer-props="{ 'items-per-page-options': [10, 15, 30, -1] }"
      :options="options"
      @update:options="handleOptionsUpdate"
    >
      > >
      <template v-slot:top>
        <v-text-field v-model="search" outlined dense label="Search" class="search-bar-responsive"></v-text-field>
      </template>
      <!-- status -->

      <template #[`item.name`]="{ item }">
        <p @click="appInformation(item)">
          {{ item.name }}
        </p>
      </template>
      <template #[`item.address`]="{ item }">
        <p @click="appInformation(item)">
          {{ item.address }}
        </p>
      </template>
      <template #[`item.email`]="{ item }">
        <p @click="appInformation(item)">
          {{ item.email }}
        </p>
      </template>
      <template #[`item.register_date`]="{ item }">
        <p @click="appInformation(item)">
          {{ item.register_date }}
        </p>
      </template>
      <template #[`item.insuranceType`]="{ item }">
        <p @click="appInformation(item)">
          {{ item.insuranceType }}
        </p>
      </template>
      <template #[`item.link`]="{ item }">
        <v-btn icon :href="'https://apply.insure/f/' + item.link" target="_blank" color="primary">
          /{{ item.link }}
        </v-btn>
      </template>

      <template #[`item.status`]="{ item }">
        <v-select
          v-model="item.status"
          :items="status"
          label="Select"
          style="margin-top: 15px"
          dense
          solo
          item-text="report"
          item-value="src"
          return-object
          persistent-hint
          @input="changeRoute(item.status, item._id)"
        ></v-select>
      </template>
    </v-data-table>
    <v-dialog v-model="dialog" max-width="1000">
      <v-card class="quoteRequestModal">
        <v-card-title class="modalBg">
          <span class="text-h5"> New Quote Request </span>
        </v-card-title>
        <v-card-text style="padding: 0px !important; color: #000 !important">
          <v-row style="padding: 50px">
            <v-col col="12" md="4">
              <div class="agentModalInfo">
                <h6>Agent:</h6>
                <div v-for="item of applicationinfo.agent" :key="item.name">
                  <h4>{{ item.name }}</h4>
                  <h5>{{ item.phone }}</h5>
                  <a :href="'mailto:' + item.email">{{ item.email }}</a>
                </div>
              </div>
            </v-col>
            <v-col col="12" md="4">
              <div class="agentModalImage">
                <img src="@/assets/umbrella.png" alt="" />
              </div>
            </v-col>
            <v-col col="12" md="4">
              <div class="agentModalInfo">
                <h6>Client:</h6>
                <h4>{{ applicationinfo.name }}</h4>
                <h5>{{ applicationinfo.phone }}</h5>
                <a :href="'mailto:' + applicationinfo.email">{{ applicationinfo.email }}</a>
              </div>
            </v-col>
          </v-row>
          <v-col class="modalBg">
            <v-container>
              <v-col col="12" class="houseHoldInfo">
                <h6>HOUSEHOLD MEMBERS</h6>
                <div class="d-flex" style="justify-content: space-evenly;text-center">
                  <h4><i class="fa-solid fa-user-large fa-2x"></i></h4>
                  <h4><i class="fa-solid fa-cake-candles fa-2x"></i></h4>
                  <h4><i class="fa-solid fa-id-card fa-2x"></i></h4>
                </div>
                <div v-for="member of applicationinfo.householdmember" :key="member.name">
                  <div class="d-flex" style="justify-content: space-evenly;text-center">
                    <div class="houseHoldName">
                      <h4>{{ member.name }}</h4>
                    </div>
                    <div class="houseHoldDB">
                      <h5>{{ member.birthday }}</h5>
                    </div>
                    <div class="houseHoldLicense">
                      <h5>{{ member.license }}</h5>
                    </div>
                  </div>
                </div>
              </v-col>
            </v-container>
          </v-col>

          <v-col>
            <v-container>
              <v-col col="12" class="applicatantAddress text-center" v-if="applicationinfo.riskManagement">
                <h5 class="mb-0">Risk Factor</h5>
                <h1>{{ applicationinfo.riskManagement }}</h1>

                <ul class="informationList text-center">
                  <li>
                    <div class="infoDiv">
                      <p><span>Question1:</span></p>
                      <p>{{ applicationinfo.riskManagementQuestions.question1 }}</p>
                    </div>
                  </li>

                  <li>
                    <div class="infoDiv">
                      <p><span>Question2:</span></p>
                      <p>{{ applicationinfo.riskManagementQuestions.question2 }}</p>
                    </div>
                  </li>
                  <li>
                    <div class="infoDiv">
                      <p><span>Question3:</span></p>
                      <p>{{ applicationinfo.riskManagementQuestions.question3 }}</p>
                    </div>
                  </li>
                  <li>
                    <div class="infoDiv">
                      <p><span>Question4:</span></p>
                      <p>{{ applicationinfo.riskManagementQuestions.question4 }}</p>
                    </div>
                  </li>
                </ul>
              </v-col>
              <v-col cols="12"  class="applicatantAddress text-center"> 
                  <p class=" font-weight-bold " style="color: #000 !important;">Policy Start Date</p>
                <h1>{{ applicationinfo.policyStartDate }}</h1>
              </v-col>
              <v-col col="12" class="applicatantAddress text-center">
                 
                <img src="@/assets/map.png" class="img-fluid" alt="" />
                <p>Your Address</p>
                <h1>{{ applicationinfo.address }}</h1>
              </v-col>
            </v-container>

            <ul class="informationList">
              <li>
                <div class="infoDiv">
                  <p><span>Year Built:</span></p>
                  <p>{{ applicationinfo.yearBuilt }}</p>
                </div>
              </li>

              <li>
                <div class="infoDiv">
                  <p><span>Square Feet:</span></p>
                  <p>{{ applicationinfo.sqft }}</p>
                </div>
              </li>
              <li>
                <div class="infoDiv">
                  <p><span>Property To Insure:</span></p>
                  <p>{{ applicationinfo.currentadress }}</p>
                </div>
              </li>
              <li>
                <div class="infoDiv">
                  <p><span>Insurance Type:</span></p>
                  <p>{{ applicationinfo.insuranceType }}</p>
                </div>
              </li>

              <li>
                <div class="infoDiv">
                  <p><span>Own / Rent:</span></p>
                  <p>{{ applicationinfo.selectedUserType }}</p>
                </div>
              </li>
              <li>
                <div class="infoDiv">
                  <p><span>Foundation:</span></p>
                  <p>{{ applicationinfo.foundation }}</p>
                </div>
              </li>
              <li>
                <div class="infoDiv">
                  <p><span>Security System:</span></p>
                  <p>{{ applicationinfo.security_system ? 'Yes' : 'No' }}</p>
                </div>
              </li>
              <li>
                <div class="infoDiv">
                  <p><span>Dog:</span></p>
                  <p>{{ applicationinfo.dog ? 'Yes' : 'No' }}</p>
                </div>
              </li>
              <li>
                <div class="infoDiv">
                  <p><span>Age of Roof:</span></p>
                  <p>{{ applicationinfo.roof_shape }}</p>
                </div>
              </li>
              <li>
                <div class="infoDiv">
                  <p><span>Roof Type:</span></p>
                  <p>{{ applicationinfo.roof_type }}</p>
                </div>
              </li>
              <li>
                <div class="infoDiv">
                  <p><span>Short Term Rentals(airBnB,etc) :</span></p>
                  <p>{{ applicationinfo.shortrentals ? 'Yes' : 'No' }}</p>
                </div>
              </li>
              <li>
                <div class="infoDiv">
                  <p><span> Property owned by corporation or a trust:</span></p>
                  <p>{{ applicationinfo.propertyown ? 'Yes' : 'No' }}</p>
                </div>
              </li>
              <li>
                <div class="infoDiv">
                  <p><span>Multiple Unit Property?</span></p>
                  <p>{{ applicationinfo.multipleunit ? 'Yes' : 'No' }}</p>
                </div>
              </li>
              <li>
                <div class="infoDiv">
                  <p><span>Have you filed any property insurance claims?</span></p>
                  <p>{{ applicationinfo.insuranceclaims ? 'Yes' : 'No' }}</p>
                </div>
              </li>
              <li>
                <div class="infoDiv">
                  <p><span> Have you ever had a policy cancelled or non-renewed in last 3 years?</span></p>
                  <p>{{ applicationinfo.policycancell ? 'Yes' : 'No' }}</p>
                </div>
              </li>
              <li>
                <div class="infoDiv">
                  <p><span> Do you have a swimming pool ? </span></p>
                  <p>{{ applicationinfo.swimmingpool ? 'Yes' : 'No' }}</p>
                </div>
              </li>
              <li>
                <div class="infoDiv">
                  <p><span> Custom Questions 1 </span></p>
                  <p>{{ applicationinfo.answer5 ? applicationinfo.answer5 : 'No Answer' }}</p>
                </div>
              </li>
              <li>
                <div class="infoDiv">
                  <p><span> Custom Questions 2 </span></p>
                  <p>{{ applicationinfo.answer6 ? applicationinfo.answer6 : 'No Answer' }}</p>
                </div>
              </li>
              <li>
                <div class="infoDiv">
                  <p><span> Applicant IP Address </span></p>
                  <p>{{ applicationinfo.applicantIP }}</p>
                </div>
              </li>
            </ul>

            <v-container>
              <v-col col="12" class="applicatantAddress text-center">
                <img src="@/assets/car.png" class="img-fluid" alt="" />
                <p>Car Insurance</p>
              </v-col>
            </v-container>

            <ul class="informationList ">
              <li>
                <div class="carInfo">
                  <p>Year</p>
                  <p>Type</p>
                  <p>Model</p>
                  <p>VIN</p>
                </div>
              </li>
              <li v-for="item of applicationinfo.cardata" :key="item.cardatayear">
                <div class="carInfo">
                  <p>{{ item.cardatayear }}</p>
                  <p>{{ item.cartype }}</p>
                  <p>{{ item.carmodel }}</p>
                  <p>{{ item.vin }}</p>
                </div>
              </li>
            </ul>

            <div class="text-center premiumText ">
              <p class="mb-0">Current carrier</p>
              <h1>{{ applicationinfo.carrierType }}</h1>
              <p class="mb-0">Current auto premium</p>
              <h1>$ {{ applicationinfo.current_auto_premiun }}</h1>
            </div>
          </v-col>

          <v-col class="modalBg mt-5 pt-5" v-if="applicationinfo.requestorcomments">
            <v-container>
              <v-col col="12" class="houseHoldInfo">
                <h1>Notes:</h1>
                <p>{{applicationinfo.requestorcomments}}</p>
              </v-col>
            </v-container>
          </v-col>

          
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-card>
</template>
